<template>
  <div id="app" class="container">
    <nav>
      <router-link :to="{ name: 'home' }">Форма</router-link> |
      <router-link :to="{ name: 'forms' }">Сохраненные формы</router-link>
    </nav>
    <router-view />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapMutations } from 'vuex'

export default {
  methods: {
    ...mapMutations([
      'updateFormsData',
    ]),
  },
  computed: {
    ...mapGetters([
      'getFormsData',
      ])
  },
  mounted() {
    if (localStorage.userFormsData) {
      this.updateFormsData(JSON.parse(localStorage.userFormsData));
    }
  },
  watch: {
    getFormsData: {
      deep: true,
      handler() {
        localStorage.userFormsData = JSON.stringify(this.getFormsData)
      }
    }
  },
}
</script>
