<template>
  <div>
    <span>{{ name }}</span>
    <label class="form__label">
      Значение
      <input class="form__input" type="text" v-model="data">
    </label>
    <label class="form__label">
      Год
      <input class="form__input" type="number" v-model="year">
    </label>
    <button @click.prevent="sendData">
      Добавить
    </button>
    <span>Добавленные значения: {{ value.map(value => {
      return value.value + '-' + value.year
    }) }}</span>
  </div>
</template>

<script>
export default {
  props: ['name', 'value'],
  data() {
    return {
      data: '',
      year: '',
    }
  },
  methods: {
    sendData() {
      this.$emit('sendData', {
        value: this.data,
        year: this.year
      }),
      this.$emit('updateArr', {}),
      this.data = '',
      this.year = ''
    }
  }
}
</script>
